import { Textfield } from "../ui/textfield.js";

export function Endpoint(props) {
  const data = props.data;
  const url = window.location.href + "v1";
  const region = data.region;
  const puuid = data.puuid;

  if (!data) {
    return <div>Loading...</div>;
  }

  if (props.api === "streamlabs") {
    return (
      <div>
        <h3>
          Hi, {data.name} #{data.tag}
        </h3>
        <div className=" text-lg ">
          <p>
            Puuid: {data.puuid} Region: {data.region} <br />
            !command add !commandname then endpoints
          </p>
          <p>KDA: {data.kda}</p>
          <Textfield link={`$(urlfetch ${url}/kd/${region}/${puuid})`} />
          <p>Headshot</p>
          <Textfield link={`$(urlfetch ${url}/hs/${region}/${puuid})`} />

          <p>
            Rank: {data.rr.CurrentTierPatched}, RR: {data.rr.RankingInTier}
          </p>
          <Textfield link={`$(urlfetch ${url}/rr/${region}/${puuid})`} />
          <p>
            Leaderboard: {data.leaderboard}
            <span>
              {" "}
              only visible if you are on the leaderboard <br /> Use Endpoint /lbr/
              to get How far you are from radiant{" "}
            </span>
          </p>
          <Textfield link={`$(urlfetch ${url}/lb/${region}/${puuid})`} />
          <p>Win/Loss Record: {data.winloss.Record.join(", ")}</p>
          <Textfield link={`$(urlfetch ${url}/wl/${region}/${puuid})`} />
          <p>
            Wins: {data.wins} &nbsp; Losses: {data.losses} &nbsp; Draws:{" "}
            {data.draws} <br /> Use ?mode=competitive to get competitive stats only <br /> Use ?mode=competitive to get competitive stats only
          </p>
        </div>
      </div>
    );
  }

  if (props.api === "streamelements") {
    return (
      <div>
        <h3>
          Hi, {data.name} #{data.tag}
        </h3>
        <div className=" text-lg ">
          <p>
            Puuid: {data.puuid} Region: {data.region} <br /> Use !command add
            !commandname then endpoints
          </p>
          <p>KDA: {data.kda}</p>
          <Textfield link={`\${urlfetch ${url}/kd/${region}/${puuid}}`} />
          <p>Headshot</p>
          <Textfield link={`\${urlfetch ${url}/hs/${region}/${puuid}}`} />

          <p>
            Rank: {data.rr.CurrentTierPatched}, RR: {data.rr.RankingInTier}
          </p>
          <Textfield link={`\${urlfetch ${url}/rr/${region}/${puuid}}`} />
          <p>
            Leaderboard: {data.leaderboard}
            <span>
              {" "}
              only visible if you are on the leaderboard <br /> Use Endpoint /lbr/
              to get How far you are from radiant{" "}
            </span>
          </p>
          <Textfield link={`\${urlfetch ${url}/lb/${region}/${puuid}}`} />
          <p>Win/Loss Record: {data.winloss.Record.join(", ")}</p>
          <Textfield link={`\${urlfetch ${url}/wl/${region}/${puuid}}`} />
          <p>
            Wins: {data.wins} &nbsp; Losses: {data.losses} &nbsp; Draws:{" "}
            {data.draws} <br /> Use ?mode=competitive to get competitive stats only
          </p>
        </div>
      </div>
    );
  }

  if (props.api === "nightbot") {
    return (
      <div>
        <h3>
          Hi, {data.name} #{data.tag}
        </h3>
        <div className=" text-lg ">
          <p>
            Puuid: {data.puuid} Region: {data.region} <br /> !command add
            !commandname then endpoints
          </p>
          <p>KDA: {data.kda}</p>
          <Textfield link={`$(urlfetch ${url}/kd/${region}/${puuid})`} />
          <p>Headshot</p>
          <Textfield link={`$(urlfetch ${url}/hs/${region}/${puuid})`} />

          <p>
            Rank: {data.rr.CurrentTierPatched}, RR: {data.rr.RankingInTier}
          </p>
          <Textfield link={`$(urlfetch ${url}/rr/${region}/${puuid})`} />
          <p>
            Leaderboard: {data.leaderboard}
            <span>
              {" "}
              only visible if you are on the leaderboard <br /> Use Endpoint /lbr/
              to get How far you are from radiant{" "}
            </span>
          </p>
          <Textfield link={`$(urlfetch ${url}/lb/${region}/${puuid})`} />
          <p>Win/Loss Record: {data.winloss.Record.join(", ")}</p>
          <Textfield link={`$(urlfetch ${url}/wl/${region}/${puuid})`} />
          <p>
            Wins: {data.wins} &nbsp; Losses: {data.losses} &nbsp; Draws:{" "}
            {data.draws} <br /> Use ?mode=competitive to get competitive stats only
          </p>
        </div>
      </div>
    );
  }

  return (
    <div>
      <h3>
        Hi, {data.name} #{data.tag}
      </h3>
      <div className=" text-lg ">
        <p>
          Puuid: {data.puuid} Region: {data.region}
        </p>
        <p>KDA: {data.kda}</p>
        <Textfield link={`${url}/kd/${region}/${puuid}`} />
        <p>Headshot</p>
        <Textfield link={`${url}/hs/${region}/${puuid}`} />

        <p>
          Rank: {data.rr.CurrentTierPatched}, RR: {data.rr.RankingInTier}
        </p>
        <Textfield link={`${url}/rr/${region}/${puuid}`} />
        <p>
          Leaderboard: {data.leaderboard}
          <span>
            {" "}
            only visible if you are on the leaderboard <br /> Use Endpoint /lbr/ to
            get How far you are from radiant{" "}
          </span>
        </p>
        <Textfield link={`${url}/lb/${region}/${puuid}`} />
        <p>Win/Loss Record: {data.winloss.Record.join(", ")}</p>
        <Textfield link={`${url}/wl/${region}/${puuid}`} />
        <p>
        Wins: {data.wins} &nbsp; Losses: {data.losses} &nbsp; Draws:{" "}
        {data.draws} <br /> Use ?mode=competitive to get competitive stats only
        </p>
      </div>
    </div>
  );
}
